<template>
    <div class="Home pbt p3 wrapper">
        <van-tabs v-model:active="active" shrink class="out height100" @change="onChange">
            <van-tab class="dynamics height100">
                <template #title>
                    <van-badge dot :color="dynamicMaker.length ? 'red' : 'transparent'"></van-badge>
                </template>
                <div v-if="logined" class="height100">
                    <div class="bulk dynamicBulk" ref="dynamicBulk">
                        <van-pull-refresh v-model="refreshing" :head-height="80" style="min-height:100%;"
                            @refresh="onRefresh">
                            <van-tabs v-model:active="active2" shrink line-height="0" :before-change="peopleChange"
                                class="shadow" sticky>
                                <van-tab name="-1">
                                    <template #title>
                                        <div class="outBox">
                                            <div class="box">
                                                <van-icon name="weapp-nav" size='1.3rem' />
                                                <p class="p6 van-ellipsis"></p>
                                            </div>
                                        </div>
                                    </template>
                                </van-tab>
                                <van-tab v-for="(el) in follows" :key="el.userId" :name="el.userId">
                                    <template #title>
                                        <div class="outBox"
                                            :style="{ 'background': el.userId == targetId ? '#eee' : 'white' }">
                                            <div class="box">
                                                <van-badge dot :color="hasNewDynamic(el.userId)">
                                                    <van-image round width="1.3rem" height="1.3rem"
                                                        :src="avatarGet(el.avatar)" lazy-load fit="cover" />
                                                </van-badge>
                                                <p class="p6 van-ellipsis">{{ el.nickname }}</p>
                                            </div>
                                        </div>
                                    </template>
                                </van-tab>
                            </van-tabs>
                            <van-loading size="1rem" vertical v-if="loading" style="padding-top:1rem">...
                            </van-loading>
                            <van-empty image="error" description="" v-else-if="!list.length" />
                            <div v-else class="" style="height:calc(100% - 3rem)">
                                <van-list v-model:loading="dynamicLoading" :finished="dynamicFinished"
                                    finished-text="" @load="onLoad">
                                    <PostList :ArticleList="list"></PostList>
                                </van-list>
                            </div>
                        </van-pull-refresh>
                    </div>
                </div>
                <div v-else>
                    <van-empty image="error" description="" />
                    <div class="notLogin">
                        <van-button color="#000" to="/login"></van-button>
                    </div>
                </div>
            </van-tab>
            <van-tab title="" class="headline ">
                <van-tabs v-model:active="active1" shrink swipeable @change="channelChange"
                    class="height100B  bulk allChannel">
                    <van-tab :name="'-1' - 0">
                        <template #title></template>
                        <van-pull-refresh v-model="refreshingAll" :head-height="80"
                            style="min-height:calc(100vh - 96px)" @refresh="onRefreshAll">
                            <van-swipe :autoplay="3000" indicator-color="white">
                                <van-swipe-item v-for="el in swipe" :key="el.articleId">
                                    <router-link :to="'/articleHome/' + el.articleId">
                                        <div class="box">
                                            <van-image width="10rem" height="5rem" fit="cover" :src="el.cover" />
                                            <p class="van-multi-ellipsis--l2">{{ el.title }}</p>
                                        </div>
                                    </router-link>
                                </van-swipe-item>
                            </van-swipe>
                            <ChannelList :channelId="-1" :currentChannel="currentChannel" :params="channelParams" :key="allChannelKey" @refreshed="refreshed">
                            </ChannelList>
                        </van-pull-refresh>
                    </van-tab>
                    <van-tab v-for=" (el) in myChannels" :key="el.id" :name="el.channelId" class="height100B ">
                        <template #title>{{ el.channelName }}</template>
                        <ChannelList :channelId="el.channelId" :currentChannel="currentChannel" :params="channelParams"
                            ref="channel">
                        </ChannelList>
                    </van-tab>
                    <template #nav-right>
                        <div class="placeholder"></div>
                        <van-icon name="wap-nav" class="edit" @click="showPop"></van-icon>
                    </template>
                </van-tabs>
            </van-tab>
        </van-tabs>
        <van-popup v-model:show="show" position="right" :style="{ width: '100%', height: '100%' }" class="pop">
            <van-nav-bar title="" @click-left="onClickLeft" :right-text="isEditing === true ? '' : ''"
                @click-right="onClickRight">
                <template #left>
                    <van-icon name="cross" size="18" />
                </template>
            </van-nav-bar>
            <ChannelEdit :my="myChannels" :isEdit="isEditing" ref="headerChild" v-if="show"></ChannelEdit>
        </van-popup>
        <TabBar></TabBar>
    </div>
</template>
<script>
import PostList from '@/components/PostList.vue'
import { Dialog } from 'vant'
import { Toast } from 'vant';
import ChannelEdit from '@/components/ChannelEdit.vue'
import ChannelList from '@/components/ChannelList.vue'
import TabBar from '@/views/layout/TabBar.vue'
export default {
    name: 'HomeView',
    inject: ['minHeight', 'avatarGet','reload'],
    components: {
        ChannelEdit, PostList, ChannelList, TabBar
    },
    data() {
        return {
            active2: 0,//
            active: 1,//
            active1: 0,//
            logined: false,
            show: false,
            myChannels: [],
            isEditing: false,
            swipe: [],
            articleList: [],
            followList: [],//
            follows: [],//
            targetId: -2,
            list: [],
            loading: true,//loading
            dynamicMaker: [],
            originalChannel: [],
            dynamicLoading: false,//
            dynamicSkip: 0,
            dynamicFinished: false,
            dynamicParams: {},
            size: this.$store.state.size,
            currentChannel: -2,
            channelParams: {},
            refreshing: false,
            dynamicScrollTop: 0,
            scrollLocation: {},
            refreshingAll: false,
            allChannelKey:0,
        }
    },
    methods: {
        showPop() {//
            this.originalChannel = [...this.myChannels]
            this.show = true;
        },
        onRefreshAll() {
            this.allChannelKey = this.reload();
        },
        refreshed(data) {
            if (this.refreshingAll) {
                Toast('!');
                this.refreshingAll = data;
            }
        },
        async onRefresh() {//
            if (this.loading) {
                Toast.fail({
                    overlay: true,
                    message: '!'
                })
                this.refreshing = false;
                return
            }
            await this.newDynamicCheck()
            await this.peopleChange(this.targetId)
            this.refreshing = false;
        },
        onClickLeft() {//
            this.myChannels = [...this.originalChannel]
            this.show = false;
            this.isEditing = false;
        },
        async onClickRight() {//
            if (this.logined === true) {//
                if (this.isEditing === false)
                    this.isEditing = true
                else {
                    if (this.$refs.headerChild.myChannels.length < 4) {
                        Toast.fail('')
                        return
                    }
                    this.isEditing = false;
                    this.myChannels = this.$refs.headerChild.myChannels;
                    let arr = [];
                    arr = this.$refs.headerChild.myChannels.map(el => {
                        return el.channelId
                    })
                    const params = { userId: this.$store.state.user.userId, channels: arr }
                    await this.$axios.get('channelUpdate', { params })
                    this.show = false;
                    this.isEditing = false;
                }
            }
            else {
                Dialog.confirm({
                    title: '',
                    message:
                        '?',
                })
                    .then(() => {
                        this.$router.replace('/login');
                    })
                    .catch(() => {
                        // on cancel
                    });
            }
        },
        async hadLogin() {//
            this.logined = true;
            const params = { userId: this.$store.state.user.userId }
            // userDone
            // await this.$axios.get('userDoneUpdate', { params })
            //     .then(rs => {
            //     console.log(rs,'userDone');
            // })
            await this.$axios.get('chaListShow', { params }).then(rs => {
                this.myChannels = rs[0];
                this.channelChange(-1);
            })
            await this.$axios.get('subscribeListGet', { params }).then(rs => {//
                this.followList = rs[0];
            })
            await this.$axios.get('dynamicMakerIdGet', { params: { userId: this.$store.state.user.userId } }).then(rs => {//
                this.dynamicMaker = rs;
            })
            const params1 = { followList: this.dynamicMaker }//
            await this.$axios.get('subscribeDetailGet', { params: params1 }).then(rs => {
                this.follows = rs[0];
            })
            await this.peopleChange(-1);
        },
        notLogin() {//
            this.logined = false
            this.$axios.get('chaAllGet',).then(rs => {
                this.myChannels = rs;
                this.channelChange(-1);
            })
        },
        async peopleChange(name) {
            this.dynamicSkip = 0,
                this.list = [],
                this.loading = true;
            this.targetId = name;
            if (!this.followList.length) {
                this.list = [];
                setTimeout(() => {
                    this.loading = false;
                }, 100);
                return
            }
            if (name == -1)
                this.dynamicParams = { followList: this.followList, URL: this.$store.state.URL }//
            else {
                //
                this.dynamicMaker.splice(this.dynamicMaker.indexOf(name), 1)
                await this.$axios.get('dynamicRead', { params: { userId: this.$store.state.user.userId, dynamic: this.dynamicMaker } })//
                this.dynamicParams = { URL: this.$store.state.URL, userId: name }
            }
            this.onLoad()
        },
        hasNewDynamic(userId) { //
            if (this.dynamicMaker.indexOf(userId) === -1)
                return 'transparent'
            else
                return 'red'
        },
        async onLoad() { //
            const params = { ...this.dynamicParams, skip: this.dynamicSkip, size: this.size }
            await this.$axios.get('dynamicsGet', { params }).then(rs => {
                setTimeout(() => {
                    this.list = [...this.list, ...rs];
                    if (this.dynamicSkip === 0)
                        this.loading = false;
                    this.dynamicLoading = false;
                    if (rs.length < this.size) {
                        this.dynamicFinished = true;
                    }
                    else {
                        this.dynamicSkip = this.dynamicSkip + this.size
                    }
                }, 100);
            })
        },
        async newDynamicCheck() {
            await this.$axios.get('dynamicMakerIdGet', { params: { userId: this.$store.state.user.userId } }).then(rs => {//
                this.dynamicMaker = rs;
            })
            const params1 = { followList: this.dynamicMaker }//
            await this.$axios.get('subscribeDetailGet', { params: params1 }).then(rs => {
                this.follows = rs[0];
            })
        },
        onChange(name) {
            if (!name) {
                this.$nextTick(() => {//
                    document.querySelector('.dynamicBulk').addEventListener('scroll', this.scrollHandler, false)
                    document.querySelector(".dynamicBulk").scrollTop = this.dynamicScrollTop;
                })
            }
            else {//
                this.$nextTick(() => {
                    document.querySelector(".allChannel").scrollTop = this.scrollLocation[this.active1];
                })
            }
        },
        channelChange(name) {//
            this.$nextTick(() => {//
                document.querySelector(".allChannel").scrollTop = this.scrollLocation[name];
            })
            this.currentChannel = name;
            if (!this.logined) {
                this.channelParams = { channelId: name, URL: this.$store.state.URL }
            }
            else {
                this.channelParams = { channelId: name, URL: this.$store.state.URL, userId: this.$store.state.user.userId }
            }
        },
        scrollHandler() {//
            switch (this.active) {
                case 0:
                    this.dynamicScrollTop = this.$refs.dynamicBulk.scrollTop; break;
                case 1:
                    this.scrollLocation[this.active1] = document.querySelector('.allChannel').scrollTop;
                    break;
            }
            // console.log('dynamic->', this.dynamicScrollTop, "scrollLocation->", this.scrollLocation);
        },
        async announceGet() {
            let message;
            let title;
            await this.$axios.get('announceGet').then(rs => {
                message = rs.message;
                title = rs.type;
            })
            if ((!this.$store.state.lastDate || Date.now() - this.$store.state.lastDate >= 24 * 60 * 60 * 1000) && message.length !== 0) {
                Dialog.alert({
                    title: title,
                    message,
                }).then(() => {
                    this.$store.commit('setLastDate', Date.now())
                });
            }
        }
    },
    mounted() {
        this.announceGet()
        //
        if (this.$store.state.user) {
            this.hadLogin();
        }
        else {
            this.notLogin()
        }
        //
        const params = { url: this.$store.state.URL }
        this.$axios.get('swipeGet', { params }).then(rs => {//
            this.swipe = rs
        })
        this.$nextTick(() => {
            document.querySelector('.allChannel').addEventListener('scroll', this.scrollHandler)
        })
    },
    activated() {
        //
        if (this.$route.meta.needRefresh === true) {
            Toast.loading({
                message: '...',
                overlay: true,
                duration: 2000,
                onClose: (() => {
                    window.ddd();
                })
            })
        }
        else {
            //
            if (this.$store.state.user) {
                this.newDynamicCheck();
            }
            this.$nextTick(() => {//
                if (this.active) {
                    document.querySelector(".allChannel").scrollTop = this.scrollLocation[this.active1];
                }
                else if (!this.active) {
                    document.querySelector(".dynamicBulk").scrollTop = this.dynamicScrollTop;
                }
            })
        }
    },
    beforeRouteEnter(to, from, next) {
        if (from.path.indexOf('/login') !== -1 || from.path.indexOf('channelSelect') !== -1) {
            to.meta.needRefresh = true;
        }
        else
            to.meta.needRefresh = false
        next()
    },
    beforeRouteLeave(to, from, next) {
        // if (this.active&&this.active1===-1) {//
        //     this.allChannelScrollTop = document.querySelector(".allChannel").scrollTop;
        // }
        // else if (!this.active && this.active1 === -1)//
        //     this.dynamicScrollTop = document.querySelector(".dynamicBulk").scrollTop;
        next()
    },
    deactivated() {
    }
}
</script>

<style scoped lang="less">
.out {
    height: 100%;
}

.shadow {
    box-shadow: 0 0px 5px 0 grey;
}


.notLogin {
    display: flex;
    justify-content: center;
}

#placeholder {
    position: fixed;
    z-index: 100;
    top: 46px;
    height: 44px;
    width: 100%;
    background: white;
}

:deep(.out>.van-tabs__wrap) {
    position: fixed;
    z-index: 100;
    top: 0;
}

:deep(.out>.van-tabs__content) {
    height: 100%;
}

:deep(.headline .van-tabs__wrap) {
    position: fixed;
    z-index: 100;
    top: 46px;
}

:deep(.grid .van-grid-item__content) {
    padding: 5px;
}

// :deep(.bulkBox>.van-tabs--line) {
//     height: 100%;
//     overflow-y: auto;
// }

.Home {
    .headline {
        padding-top: 44px;
        height: 100%;

        .placeholder {
            flex-shrink: 0;
            width: 44px;
            height: 44px;
        }

        .edit {
            position: fixed;
            right: 0;
            display: flex;
            justify-content: center;
            // padding-top:10px ;
            align-items: center;
            width: 44px;
            height: 44px;
            background: rgb(255, 255, 255);
            // border-left: grey solid 2px;
            box-sizing: border-box;

        }

        .box {
            position: relative;

            p {
                width: 100%;
                padding: 0 10px;
                box-sizing: border-box;
                position: absolute;
                bottom: 20px;
                color: white;
                text-align: center;
            }
        }
    }

    .dynamics {
        div.outBox {
            padding: 10px;
            box-sizing: border-box;

            .box {
                display: flex;
                flex-direction: column;

                p {
                    text-align: center;
                    width: 1.3rem;
                }
            }
        }

        :deep(.van-tab--shrink, .van-tab--shrink) {
            padding: 0;
        }

        :deep(.van-tabs__wrap) {
            height: 3rem;
        }

    }
}

.heightStart {
    :deep(.van-tabs__content) {
        height: 100%;
    }

    .height100B {
        height: 100% !important;
    }

    :deep(.height100B .van-tab__panel) {
        height: 100%;
    }
}
</style>