<template>
    <div class="collectBox ">
        <van-action-sheet v-model:show="actionsShow" cancel-text="" title="" :actions="actions"
            close-on-click-action @select="onSelect">
        </van-action-sheet>
        <van-dialog v-model:show="dialogShow" :title="createCollect" show-cancel-button theme="round-button"
            confirm-button-color="#000" confirm-button-text="" cancel-button-color="grey" @confirm="createConfirm">
            <template #default>
                <van-field v-model="cbTitle" placeholder="(8)" required size="large" maxlength='8'
                    input-align="center" />
            </template>
        </van-dialog>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    name: 'postList',
    props: {
        articleId: {
            type: Number,
        },
        // checkedArr:{
        //     type:Array
        // }
    },
    data() {
        return {
            actionsShow: false,
            createCollect: '',
            dialogShow: false,//
            collectBox: [],//
            cbTitle: '',//
        }
    },
    methods: {
        async createConfirm() {//
            if (this.cbTitle === '') {
                Toast('');
                return
            }
            let obj = { name: this.cbTitle, content: [] }
            let arr = this.actions.filter(el => {
                return el.name === this.cbTitle;
            })
            if (arr.length !== 0) {
                Toast('');
                return
            }
            this.collectBox = [...this.collectBox, obj]
            await this.collectUpdate(this.collectBox);
            this.actionsShow = true;
        },
        async onSelect(item) {//,
            if (item.name === this.createCollect) {//
                this.dialogShow = true;
            }
            else {//
                if (this.articleId) {//
                    this.collectBox = this.collectBox.map(el => {
                        if (el.name === item.name)
                            el.content.push(this.articleId)
                        return el
                    })
                    await this.collectUpdate(this.collectBox);
                    this.$emit('hadCollected', true)
                    Toast('')
                }
                else {
                    this.$emit('move', item.name)
                }
            }
        },
        async remove(id) {//
            this.collectBox = this.collectBox.map(el => {
                if (el.content.indexOf(id) > -1)//articleId
                {
                    el.content = el.content.filter(item => {//articleId
                        return item !== id
                    })
                }
                return el
            })
            this.$emit('hadCollected', false)
            await this.collectUpdate(this.collectBox);
            Toast('')
        },
        async collectUpdate(actions) {//
            const params = { userId: this.$store.state.user.userId, actions };
            await this.$axios.get('collectUpdate', { params }).then(rs => {
                console.log(rs);
            })
        },
        collectBoxGet() {//
            const params = { userId: this.$store.state.user.userId };
            this.$axios.get('userDataShow', { params }).then(rs => {
                this.collectBox = rs.collects.collectBox;
            })
        },
    },
    mounted() {
        if (this.$store.state.user)
            this.collectBoxGet()
        // console.log('');
    },
    computed: {
        actions() {
            const add = { name: this.createCollect, subname: '' }
            return [...this.collectBox, add]
        }
    },
    updated() {
        // console.log('');
    }
}
</script>

