<template>
    <div class="collectDetail wrapper pt">
        <van-nav-bar :title="boxName" left-arrow @click-left="onClickLeft" fixed>
            <template #right>
                <van-icon name="weapp-nav" size="18" color='black' @click='this.show = true' v-if="!isCollect" />
                <span v-else @click='this.isCollect = false'></span>
            </template>
        </van-nav-bar>
        <van-tabs v-model:active="active" swipeable shrink class="bulk" id="collectDetailBulk">
            <van-tab title="">
                <van-loading size="1rem" vertical v-if="loading" style="padding-top:1rem">...</van-loading>
                <div v-show="!loading">
                    <van-empty description="" style="height:10rem" v-if='list.length === 0' />
                    <div>
                        <PostList :ArticleList="list" :isCollect="isCollect" ref="PostList" :key="key"></PostList>
                    </div>
                </div>
            </van-tab>
            <van-tab title="">
                <van-empty description="" style="height:10rem" />
            </van-tab>
        </van-tabs>
        <!-- // -->
        <van-action-sheet v-model:show="show" :actions="actions" cancel-text="" close-on-click-action title=""
            @select="onSelect" />
        <van-dialog v-model:show="dialogShow" title="" show-cancel-button theme="round-button"
            confirm-button-color="#000" confirm-button-text="" cancel-button-color="grey" @confirm="createConfirm">
            <template #default>
                <van-field v-model="cbTitle" placeholder="(8)" required size="large" maxlength='8'
                    input-align="center" />
            </template>
        </van-dialog>
        <!--  -->
        <CollectBox ref="CollectBox" @move="move" :key="key"></CollectBox>
        <div class="bottomBtn" v-show="isCollect">
            <van-row gutter="40" justify="center">
                <van-col span="12">
                    <van-button type="default" block @click="showSelect"></van-button>
                </van-col>
                <van-col span="12">
                    <van-button type="danger" block @click="delConfirm"></van-button>
                </van-col>
            </van-row>
        </div>
    </div>
</template>

<script>
import PostList from '@/components/PostList.vue'
import { Toast } from 'vant';
import { Dialog } from 'vant';
import CollectBox from '@/components/CollectBox.vue';
export default {
    name: 'CollectDetail',
    inject: ['reload'],
    components: {
        PostList,
        CollectBox
    },
    data() {
        return {
            dialogShow: false,//
            cbTitle: '',//()
            show: false,//
            boxName: '',
            active: 0,
            collectBox: [],
            list: [],
            isCollect: false,
            actions: [//
                // { name: '' },
                { name: '' },
                { name: '' },
                { name: '', color: 'red' },
            ],
            checkedArr: [],
            loading: true,
            key: 1,
            scrollTop: 0,
            overdue: [],
        }
    },
    methods: {
        showSelect() {//
            this.checkedArr = this.$refs.PostList.checkedId;
            if (!this.checkedArr.length)
                return
            this.$refs.CollectBox.actionsShow = true;
        },
        async move(name) {
            const params = { userId: this.$store.state.user.userId, boxName: this.boxName, URL: this.$store.state.URL }
            await this.$axios('collectDetail', { params }).then(rs => {
                this.collectBox = rs[1];
            })
            if (name === this.boxName) {
                Toast('')
                return
            }
            Dialog.confirm({
                title: '',
                message:
                    '' + name + '',
            })
                .then(() => {
                    this.collectBox = this.collectBox.map(el => {
                        if (el.name === name)
                            el.content = [...el.content, ...this.checkedArr]
                        return el
                    })
                    this.del();
                    // this.checkedArr.length.length=0;

                    this.collectUpdate()
                }).catch(() => {
                });
        },
        del() {
            this.collectBox = this.collectBox.map(el => {
                if (el.name === this.boxName)
                    el.content = el.content.filter(item => {
                        return this.checkedArr.indexOf(item) === -1//id
                    })
                this.list = this.list.filter(item => {
                    return this.checkedArr.indexOf(item.articleId) === -1//list
                })
                return el
            })
            this.$refs.PostList.checkedId.length = 0;
        },
        collectUpdate() {
            const params = { userId: this.$store.state.user.userId, actions: this.collectBox }
            this.$axios('collectUpdate', { params }).then(rs => {
                console.log('', rs);
                Toast('');
            })
        },
        delConfirm() {//
            this.checkedArr = this.$refs.PostList.checkedId;
            if (!this.checkedArr.length)
                return
            Dialog.confirm({
                title: '',
                message:
                    '',
            })
                .then(() => {
                    this.del()
                    // this.checkedArr.length;
                    this.collectUpdate()
                }).catch(() => {
                    // on cancel
                });
        },
        onClickLeft() {
            this.$router.back();
        },
        createConfirm() {//
            if (this.cbTitle === '') {
                Toast('');
                return
            }
            let arr = []
            arr = this.collectBox.filter(el => {
                return el.name === this.cbTitle;
            })
            if (arr.length !== 0) {
                Toast('');
                return
            }
            arr = this.collectBox.map(el => {
                if (el.name === this.boxName) {
                    el.name = this.cbTitle;
                    this.boxName = this.cbTitle
                }
                return el
            })
            const params = { actions: this.collectBox, userId: this.$store.state.user.userId }
            this.$axios.get('collectUpdate', { params })
        },
        onSelect(item) {//
            if (item.name === '') {
                Dialog.confirm({
                    title: '',
                    message:
                        '',
                })
                    .then(() => {
                        let arr = this.collectBox.filter(el => {
                            return el.name !== this.boxName
                        })
                        const params = { userId: this.$store.state.user.userId, actions: arr }
                        this.$axios('collectUpdate', { params }).then(rs => {
                            console.log('', rs);
                            Toast('');
                            this.$router.back();
                        })
                    })
                    .catch(() => {

                    });
            }
            if (item.name === '') {
                this.dialogShow = true;
            }
            if (item.name === '') {
                this.isCollect = true;
            }
            if (item.name === '') {
                this.collectBox = this.collectBox.filter(el => {
                    if (el.name === this.boxName)
                        el.content = el.content.filter(item => {
                            return this.overdue.indexOf(item) === -1;
                        })
                    return el
                })
                console.log(this.collectBox);
                this.list = this.list.filter(item => {
                    return !item.notExist
                })
                this.collectUpdate()
            }
        },
        loaded() {
            if (this.loading === true)
                setTimeout(() => {
                    this.loading = false;
                }, 500)
        },
        dataGet() {
            this.boxName = this.$route.query.boxName;
            if (this.boxName === '') {
                this.actions = [{ name: '' }, { name: '' },]
            }
            const params = { userId: this.$store.state.user.userId, boxName: this.boxName, URL: this.$store.state.URL }
            this.$axios('collectDetail', { params }).then(rs => {
                this.collectBox = rs[1];
                this.list = rs[0];
                this.overdue = rs[2];
                this.loaded();
            })
        }
    },
    mounted() {
        this.dataGet()
    },
    activated() {
        if (this.$route.meta.isBack === false) {//
            Object.assign(this.$data, this.$options.data())
            this.dataGet();
            this.key = this.reload()
        }
        else {
            this.$nextTick(() => {
                document.querySelector("#collectDetailBulk").scrollTop = this.scrollTop;
            })
        }
    },
    beforeRouteEnter(to, from, next) {
        if (from.path.indexOf('myCollect') !== -1) { to.meta.isBack = false; }
        next()
    },
    deactivated() {
        this.scrollTop = document.querySelector("#collectDetailBulk").scrollTop
    },
    updated() {

    }
}
</script>

<style scoped lang="less">
div.bottomBtn {
    position: fixed;
    height: 50px;
    width: 10rem;
    bottom: 0;
    background: rgb(232, 232, 232);
    padding: 5px 0;
}
</style>